body {
  background-color: #f783e3;
  margin: 20px;
  padding: 0;
  font-family: "Raleway", sans-serif;
}

h1 {
  font-size: 68px;
  color: #fff;
  text-align: center;
}

h2 {
  color: #fff;
  margin: 10px;
  padding-right: 10px;
  text-align: center;
}

p {
  font-size: 10px;
  color: #fff;
}

.App {
  margin-left: auto;
  margin-right: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.header {
  width: 1200px;
  margin: 30px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: baseline;
      align-items: baseline;
}

#SR {
  margin-right: 50px;
}

#radio {
  width: 100px;
  height: 100px;
}

.station {
  width: 300px;
  height: 500px;
  margin: 20px;
  padding-bottom: 40px;
  border: 4px solid #fff;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.stationImage img {
  width: 100%;
}

.stationTagline {
  padding:10px;
}

.stationAudio {
  width: 200px;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  bottom: 10px;
}



